.delete-btn {
  cursor: pointer;
  background-color: #e74c3c;
  border: 0;
  color: #fff;
  font-size: 40px;
  line-height: 20px;
  padding: 2px 5px;
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 15px;
  left: 0;
  opacity: 0;
  visibility: hidden;
  z-index: 1;
  -webkit-transition: opacity 0.3s ease;
  transition: opacity 0.3s ease;
